<template>
  <div class="checkout">
    <h1>CHECKOUT</h1>
    <div class="checkout__container">
      <!-- login -->
      <transition name="fade">
        <div id="checkout__login" class="checkout__block" v-if="user.id === ''">
          <p class="checkout__title checkout__title--collapse"
          :class="loginCollapse ? null : 'collapsed'"
          @click.prevent="loginCollapse = !loginCollapse">
          <span>
            登入 / 註冊會員
          </span>
          </p>
          <b-collapse v-model="loginCollapse">
            <div class="checkout__body d-flex justify-content-center align-items-center">
              <button class="btn btn-primary" @click="go_page('/front/login')">會員登入 / 註冊</button>
            </div>
          </b-collapse>
        </div>
      </transition>
      <!-- cart confirming -->
      <transition name="fade">
        <div class="checkout__block" v-if="!cartContentConfirmed">
          <p class="checkout__title checkout__title--collapse"
          :class="cartConfirmingCollapse ? null : 'collapsed'"
          @click.prevent="cartConfirmingCollapse = !cartConfirmingCollapse">
          <span>
            確認購物車內容
          </span>
          </p>
          <b-collapse v-model="cartConfirmingCollapse">
            <div class="checkout__body">
              <cart-content :editable='true' :connectObj='cart'></cart-content>
            </div>
            <div class="checkout__func">
              <a href="#" class="myBtn myBtn--primary"
              @click.prevent='get_newOrder_cart'>下一步</a>
            </div>
          </b-collapse>
        </div>
      </transition>
      <!-- cart confirmed -->
      <transition name="fade">
        <div class="checkout__block" v-if="cartContentConfirmed">
          <p class="checkout__title checkout__title--collapse"
          :class="cartConfirmedCollapse ? null : 'collapsed'"
          @click.prevent="cartConfirmedCollapse = !cartConfirmedCollapse">
          <span>
            確認訂購產品
          </span>
          </p>
          <b-collapse v-model="cartConfirmedCollapse">
            <div class="checkout__body">
              <cart-content :editable='false' :connectObj='cart'></cart-content>
            </div>
            <div class="checkout__func">
              <a href="#" class="myBtn myBtn--primary"
              @click.prevent='reset_newOrder_cart'>返回編輯購物車</a>
            </div>
          </b-collapse>
        </div>
      </transition>
      <!-- sum -->
      <transition name="fade">
        <div class="checkout__block" v-if="cartContentConfirmed">
          <p class="checkout__title checkout__title--collapse"
          :class="sumCollapse ? null : 'collapsed'"
          @click.prevent="sumCollapse = !sumCollapse">
          <span>
            金額總計
          </span>
          </p>
          <b-collapse v-model="sumCollapse">
            <div class="checkout__body">
              <div class="checkout__coupon">
                <div class="checkout__coupon__input myForm">
                  <span>使用優惠券: </span>
                  <input id="newOrderCouponId" type="text" class="form-control">
                  <a href="#" class="myBtn myBtn--secondary"
                  @click.prevent='get_newOrder_coupon'>使用</a>
                  <a href="#" class="myBtn myBtn--secondary"
                  v-if="newOrder.coupon.id !== ''"
                  @click.prevent='reset_newOrder_coupon'>取消使用優惠券</a>
                </div>
                <div class="checkout__coupon__lookup">
                  <a href="#" class="myBtn myBtn--info" v-if="user.id !== ''"
                  >查詢優惠券</a>
                  <a href="#" class="myBtn myBtn--info" v-if="user.id === ''"
                  @click.prevent="scroll_to_login">登入 / 加入會員查詢優惠券</a>
                </div>
              </div>
              <p class="checkout__jcfe">金額小計: NT${{newOrder.cart.sum}}</p>
              <p class="checkout__jcfe">折扣碼 ID:
                <span v-if="newOrder.coupon.id === ''">無</span>
                <span v-if="newOrder.coupon.id !== ''">{{newOrder.coupon.id}}</span>
              </p>
              <p class="checkout__jcfe">折扣: {{newOrder.coupon.discount}}</p>
              <p class="checkout__jcfe">金額總計: {{newOrder.sum}}</p>
            </div>
            <div class="checkout__func">
              <a href="#" class="myBtn myBtn--primary"
              @click.prevent='orderInfoCollapse = true'>下一步</a>
            </div>
          </b-collapse>
        </div>
      </transition>
      <!-- order info -->
      <transition name="fade">
        <div class="checkout__block" v-if="cartContentConfirmed">
          <p class="checkout__title checkout__title--collapse"
          :class="orderInfoCollapse ? null : 'collapsed'"
          @click.prevent="orderInfoCollapse = !orderInfoCollapse">
          <span>
            訂購人資訊
          </span>
          </p>
          <b-collapse v-model="orderInfoCollapse">
            <div class="checkout__body">
              <form class="checkout__form" @submit.prevent='emit_order'>
                <!-- buyer -->
                <div class="checkout__form__block">
                  <p class="checkout__form__title">購買人</p>
                  <div class="checkout__form__body">
                    <div class="myForm">
                      <div class="myForm__title">姓名</div>
                      <input type="text" class="form-control"
                      v-model="newOrder.buyer.name" required>
                    </div>
                    <div class="myForm">
                      <div class="myForm__title">聯絡電話</div>
                      <input type="tel" class="form-control"
                      v-model="newOrder.buyer.tel" required>
                    </div>
                    <div class="myForm">
                      <div class="myForm__title">Email</div>
                      <input type="email" class="form-control"
                      v-model="newOrder.buyer.email" required>
                    </div>
                  </div>
                </div>
                <!-- invoice -->
                <div class="checkout__form__block">
                  <p class="checkout__form__title">發票資料</p>
                  <div class="checkout__form__body">
                    <div class="myForm">
                      <div class="myForm__title">發票類型</div>
                      <select v-model="newOrder.invoice.type" required class="form-control"
                      @change.prevent="get_newOrder_invoice($event.target.value)">
                        <option value="" disabled>請選擇發票類型</option>
                        <option value="person">個人電子發票</option>
                        <option value="donation">捐贈電子發票</option>
                        <option value="company">公司報帳電子發票</option>
                      </select>
                    </div>
                    <div class="myForm" v-if="newOrder.invoice.type === 'person'">
                      <div class="myForm__title">載具類別</div>
                      <select v-model="newOrder.invoice.personCarrier" class="form-control"
                      @change.prevent="get_newOrder_invoice_personCarrier($event.target.value)">
                        <option value="" disabled>請選擇載具類別</option>
                        <option value="no">不使用載具</option>
                        <option value="mobile">手機條碼</option>
                        <option value="nature">自然人憑證</option>
                      </select>
                    </div>
                    <div v-if="newOrder.invoice.type === 'person'">
                      <div class="myForm"
                      v-if="newOrder.invoice.personCarrier === 'no'">
                        <div class="myForm">
                        <address-input
                        :title='"中獎發票寄送地址"'
                        @emit_address='get_personCarrierAddress'></address-input>
                        </div>
                      </div>
                    </div>
                    <div v-if="newOrder.invoice.type === 'person'">
                      <div class="myForm"
                      v-if="newOrder.invoice.personCarrier === 'mobile'">
                        <div class="myForm__title">手機載具號碼</div>
                        <input type="text" class="form-control"
                        v-model="newOrder.invoice.personCarrierMobile">
                      </div>
                    </div>
                    <div v-if="newOrder.invoice.type === 'person'">
                      <div class="myForm"
                      v-if="newOrder.invoice.personCarrier === 'nature'">
                        <div class="myForm__title">自然人憑證號碼</div>
                        <input type="text" class="form-control"
                        v-model="newOrder.invoice.personCarrierNature">
                      </div>
                    </div>
                    <div class="myForm" v-if="newOrder.invoice.type === 'donation'">
                      <div class="myForm__title">捐贈單位</div>
                      <select v-model="newOrder.invoice.donationTarget" class="form-control">
                        <option value="" disabled>請選擇捐贈單位</option>
                        <option value="donationCompany1">捐贈單位1</option>
                        <option value="donationCompany2">捐贈單位2</option>
                        <option value="donationCompany3">捐贈單位3</option>
                      </select>
                    </div>
                    <div class="myForm" v-if="newOrder.invoice.type === 'company'">
                      <div class="myForm__title">買受人</div>
                      <input type="text" class="form-control"
                      v-model="newOrder.invoice.companyTitle">
                    </div>
                    <div class="myForm" v-if="newOrder.invoice.type === 'company'">
                      <div class="myForm__title">統一編號</div>
                      <input type="text" class="form-control"
                      v-model="newOrder.invoice.companyId">
                    </div>
                  </div>
                </div>
                <!-- recipient -->
                <div class="checkout__form__block">
                  <p class="checkout__form__title">收件人資料</p>
                  <div class="checkout__form__body">
                    <div class="myForm">
                      <label class="myForm__checkbox">
                        <input type="checkbox" v-model="newOrder.recipient.ifGetFromStore"
                        @change="toggle_ifGetFromStore($event.target.checked)">
                        <span class="myForm__checkbox__span">
                          門市自行取貨
                        </span>
                      </label>
                      <span>電話: (02) 1234-5678</span>
                      <span>地址: 100臺北市中正區重慶南路一段122號</span>
                    </div>
                    <div class="myForm" v-if="!newOrder.recipient.ifGetFromStore">
                      <div class="myForm__title">姓名</div>
                      <input type="tel" class="form-control"
                       v-model="newOrder.recipient.info.name" required>
                    </div>
                    <div class="myForm" v-if="!newOrder.recipient.ifGetFromStore">
                      <div class="myForm__title">聯絡電話</div>
                      <input type="tel" class="form-control"
                       v-model="newOrder.recipient.info.tel" required>
                    </div>
                    <div class="myForm" v-if="!newOrder.recipient.ifGetFromStore">
                      <address-input
                      :title='"地址"'
                      @select_county='get_weatherForcast'
                      @emit_address='get_recipientAddress'></address-input>
                    </div>
                    <div class="myForm" v-if="!newOrder.recipient.ifGetFromStore">
                      <label class="myForm__checkbox">
                        <input type="checkbox" class="form-control"
                         v-model="newOrder.recipient.info.ifRepresentative"
                        @change="toggle_ifRepresentative($event.target.checked)">
                        <span class="myForm__checkbox__span">
                          公司 / 大樓代收
                        </span>
                      </label>
                    </div>
                    <div v-if="!newOrder.recipient.ifGetFromStore">
                      <div class="myForm"
                      v-if="newOrder.recipient.info.ifRepresentative">
                        <div class="myForm__title">收花人公司 / 大樓名稱</div>
                        <input type="text" class="form-control"
                         v-model="newOrder.recipient.info.representativeTitle">
                      </div>
                    </div>
                    <div class="myForm" v-if="!newOrder.recipient.ifGetFromStore">
                      <label class="myForm__checkbox">
                        <input type="checkbox" v-model="newOrder.recipient.info.ifAnonymous">
                        <span class="myForm__checkbox__span">
                          匿名配送
                        </span>
                      </label>
                    </div>
                    <div class="myForm">
                      <div class="myForm__title">天氣預報 ( 從本日算起 7 日 )</div>
                      <!-- 天氣預報 -->
                      <div v-if="ifForcastLoading"
                      class="spinner-border" role="status">
                        <span class="sr-only">Loading...</span>
                      </div>
                      <div v-if='ifObjHasKey'>
                        <table class="table bg-white">
                          <thead>
                            <tr>
                              <th scope="col" colspan="100">
                              {{weatherForcast.locationName}}
                              </th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr>
                              <th v-for='(timeItem, index) in weatherForcast.weatherElement[0].time'
                              :key="index">
                              {{convertTimeStr(timeItem.startTime)}}
                              </th>
                            </tr>
                            <tr>
                              <td v-for='(timeItem, index) in weatherForcast.weatherElement[0].time'
                              :key="index">
                              {{timeItem.elementValue[0].value}}
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                    <div class="myForm">
                      <div class="myForm__title">預定到貨 / 取貨日期</div>
                      <input type="date" class="form-control"
                      v-model="newOrder.recipient.esdDate" required>
                    </div>
                    <div class="myForm">
                      <div class="myForm__title">希望到貨 / 取貨時段</div>
                      <select v-model="newOrder.recipient.esdTime" class="form-control">
                        <option value="">不指定</option>
                        <option value="morning">上午( 08:00 ~ 13:00 )</option>
                        <option value="afternoon">下午( 14:00 ~ 18:00 )</option>
                      </select>
                    </div>
                    <div class="myForm">
                      <div class="myForm__title">卡片內容</div>
                      <textarea v-model='newOrder.recipient.cardContent' class="form-control"
                      placeholder="若此欄位空白，則不附卡片( 請勿使用手機表情符號 )"></textarea>
                    </div>
                    <div class="myForm">
                      <div class="myForm__title">備註</div>
                      <textarea v-model='newOrder.recipient.remark' class="form-control"
                      placeholder="商品 / 配送 / 其他特殊需求"></textarea>
                    </div>
                  </div>
                </div>
                <!-- submit form -->
                <div class="checkout__form__block checkout__form__block--submit">
                  <button type="submit" class="myBtn myBtn--primary"
                  >建立訂單，前往付款</button>
                </div>
              </form>
            </div>
          </b-collapse>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import CartContent from '@/components/CartContent.vue';
import AddressInput from '@/components/AddressInput.vue';

export default {
  components: { CartContent, AddressInput },
  computed: {
    cart() {
      let cart;
      this.$bus.$on('get_cart', (data) => {
        cart = data;
      });
      this.$bus.$emit('call_cart');
      return cart;
    },
    user() {
      let user;
      this.$bus.$on('son_get_data', (data) => {
        user = data.member;
      });
      this.$bus.$emit('son_call_data');
      return user;
    },
    coupons() {
      let coupons;
      this.$bus.$on('son_get_data', (data) => {
        coupons = data.coupons;
      });
      this.$bus.$emit('son_call_data');
      return coupons;
    },
    ifObjHasKey() {
      let boolean;
      const keyLength = Object.keys(this.weatherForcast).length;
      if (keyLength === 0) {
        boolean = false;
      } else {
        boolean = true;
      }
      return boolean;
    },
  },
  data() {
    return {
      newOrder: {
      },
      weatherForcast: {},
      ifForcastLoading: false,

      // fading block
      cartContentConfirmed: false,

      // collapse status
      loginCollapse: false,
      cartConfirmingCollapse: true,
      cartConfirmedCollapse: true,
      sumCollapse: true,
      orderInfoCollapse: false,
    };
  },
  watch: {
    'user.id': function () {
      this.create_newOrder();
    },
  },
  methods: {
    go_page(route) {
      this.$router.push(route);
    },
    scroll_to_login() {
      const nodeTop = $('#checkout__login').offset().top;
      this.loginCollapse = true;
      $('html').animate({
        scrollTop: nodeTop - 150,
      });
    },
    create_newOrder() {
      const orderTime = (new Date()).getTime();
      const orderId = `PO${orderTime}`;
      this.newOrder = {
        sum: 0,
        createTime: orderTime,
        id: orderId,
        memberId: this.user.id,
        cart: {},
        coupon: {},
        buyer: {
          name: this.user.name,
          tel: this.user.tel,
          email: this.user.email,
        },
        invoice: {
          type: '',
        },
        recipient: {
          ifGetFromStore: false,
          info: {
            name: '',
            tel: '',
            address: '',
            ifRepresentative: false,
            representativeTitle: '',
          },
          ifAnonymous: false,
          esdDate: '',
          esdTime: '',
          cardContent: '',
          remark: '',
        },
        payment: {
          ifPaid: false,
        },
      };
    },
    get_newOrder_cart() {
      const copyCart = JSON.parse(JSON.stringify(this.cart));
      this.newOrder.cart = copyCart;
      this.reset_newOrder_coupon();
      this.sum_newOrder();

      this.cartConfirmingCollapse = false;
      this.cartContentConfirmed = true;
    },
    reset_newOrder_cart() {
      this.create_newOrder();
      this.cartContentConfirmed = false;
      this.cartConfirmingCollapse = true;
    },
    get_newOrder_coupon() {
      const inputNode = document.getElementById('newOrderCouponId');
      const inputValue = inputNode.value;
      const inputId = inputValue;
      const targetCoupon = this.coupons.find((item) => item.id === inputId);

      // if (沒有優惠券) {
      //   沒有優惠券
      // } else if (優惠券條件不符) {
      //   優惠券條件不符
      // } else {
      //   使用優惠券
      // }
      const couponNotExist = () => {
        let boolean;
        if (targetCoupon === undefined) {
          boolean = true;
        } else {
          boolean = false;
        }
        return boolean;
      };
      const couponLogicNotMatch = () => {
        let boolean;
        if (targetCoupon.availableLogic(this.newOrder.cart)) {
          boolean = false;
        } else {
          boolean = true;
        }
        return boolean;
      };
      if (couponNotExist()) {
        this.$bus.$emit('show_toast', 'warning', '無此優惠券');
      } else if (couponLogicNotMatch()) {
        this.$bus.$emit('show_toast', 'warning', '優惠券條件不符');
      } else {
        this.newOrder.coupon = targetCoupon;
        this.newOrder.coupon.discount = this.newOrder.coupon.discountLogic(this.newOrder.cart);
        this.$bus.$emit('show_toast', 'success', '優惠券使用成功');
        inputNode.value = '';
      }
      this.sum_newOrder();
    },
    reset_newOrder_coupon() {
      const emptyCoupon = {
        id: '',
        discount: 0,
      };
      this.newOrder.coupon = emptyCoupon;
    },
    sum_newOrder() {
      const cartSum = this.newOrder.cart.sum;
      const couponDiscount = this.newOrder.coupon.discount;
      const orderSum = cartSum + couponDiscount;

      this.newOrder.sum = orderSum;
    },
    get_newOrder_invoice(selectValue) {
      const newInvoiceObj = {
        type: selectValue,
      };
      switch (selectValue) {
        case 'person':
          newInvoiceObj.personCarrier = '';
          break;
        case 'donation':
          newInvoiceObj.donationTarget = '';
          break;
        case 'company':
          newInvoiceObj.companyTitle = '';
          newInvoiceObj.companyId = '';
          break;
        default:
          break;
      }
      this.$set(this.newOrder, 'invoice', newInvoiceObj);
    },
    get_newOrder_invoice_personCarrier(selectValue) {
      const newInvoiceObj = {
        type: 'person',
        personCarrier: selectValue,
      };
      switch (selectValue) {
        case 'no':
          newInvoiceObj.personCarrierAddress = '';
          break;
        case 'mobile':
          newInvoiceObj.personCarrierMobile = '';
          break;
        case 'nature':
          newInvoiceObj.personCarrierNature = '';
          break;
        default:
          break;
      }
      this.$set(this.newOrder, 'invoice', newInvoiceObj);
    },
    get_personCarrierAddress(addressStr) {
      this.newOrder.invoice.personCarrierAddress = addressStr;
    },
    toggle_ifGetFromStore(checkboxBoolean) {
      if (checkboxBoolean) {
        // 打勾
        // 建立recipient.info，裡面有name, tel, address, ifRepresentative
        const infoObj = {
          name: '',
          tel: '',
          address: '',
          ifRepresentative: false,
          ifAnonymous: false,
        };
        this.$set(this.newOrder.recipient, 'info', infoObj);
        // 天氣預報
        this.get_weatherForcast('臺北市');
      } else {
        // 取消打勾
        this.newOrder.recipient.info = {};
        this.weatherForcast = {};
      }
    },
    toggle_ifRepresentative(checkboxBoolean) {
      if (checkboxBoolean) {
        // 有代表可以收貨
        this.$set(this.newOrder.recipient.info, 'representativeTitle', '');
      } else {
        // 沒有代表可以收貨
        delete this.newOrder.recipient.info.representativeTitle;
      }
    },
    get_recipientAddress(addressStr) {
      this.newOrder.recipient.info.address = addressStr;
    },
    get_weatherForcast(countyValue) {
      this.ifForcastLoading = true;
      const weatherApi = 'https://opendata.cwb.gov.tw/api/v1/rest/datastore/F-D0047-091?Authorization=CWB-CF9F5E83-FCB0-4F39-B81C-9938662E27BF&format=JSON&elementName=Wx';
      const xhr = new XMLHttpRequest();
      xhr.open('GET', weatherApi, true);
      xhr.onload = () => {
        if (xhr.status === 200) {
          const response = JSON.parse(xhr.responseText);
          const locationArr = response.records.locations[0].location;
          const targetLocation = locationArr.find((item) => item.locationName === countyValue);
          const timeArr = targetLocation.weatherElement[0].time;
          const filteredTimeArr = timeArr
            .filter((item, index) => index === 0 || item.startTime.includes('06:00:00'));
          targetLocation.weatherElement[0].time = filteredTimeArr;
          this.weatherForcast = targetLocation;
          this.ifForcastLoading = false;
        }
      };
      xhr.send();
    },
    convertTimeStr(timeStr) {
      let resultStr = '';
      const convertTime = new Date(timeStr);
      const mm = convertTime.getMonth() + 1;
      const dd = convertTime.getDate();
      resultStr = `${mm}/${dd}`;
      return resultStr;
    },
    emit_order() {
      // 新增newOrder.sending
      this.newOrder.sending = {
        ifSent: false,
      };
      // 上傳訂單
      this.$bus.$emit('add_new_order', this.newOrder);
      this.$bus.$emit('show_toast', 'success', `訂單建立完成，您的訂單編號為 ${this.newOrder.id}`);
      this.orderInfoCollapse = false;
      this.go_page(`/front/checkout_payment/${this.newOrder.id}`);
    },
  },
  created() {
    this.create_newOrder();
    this.$bus.$emit('toggleCart', false);
  },
};
// front/checkout_payment/PO1615019980462
</script>
